* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

p {
  margin-top: 20px;
  margin-bottom: 20px;
  line-height: 20px;
  font-size: 16px;
  text-align: center;
}

nav {
  color: white;
  background-color: #c4302b;
  padding: 20px 20px;
}

main {
  display: flex;
  justify-content: center;
  padding: 20px 20px;
  text-align: center;
}

.logo {
  max-width: 450px;
  width: 100%;
  padding: 50px 50px;
  display: block;
  height: auto;
}

.thumbnail {
  max-width: 650px;
  width: 100%;
  padding: 10px;
  height: auto;
}

input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  border: 2px solid #c4302b;
  border-radius: 5px;
  font-size: medium;
}

.btn {
  background-color: white;
  color: #c4302b;
  cursor: pointer;
  padding: 15px 32px;
  border: 2px solid #c4302b;
  text-align: 0 auto;
  border-radius: 5px;
  font-size: medium;
  transition-duration: 0.4s;
  margin-top: 1.5rem;
}

a {
  text-decoration: none;
}

i {
  margin-right: 10px;
}

.btn:hover {
  background-color: #c4302b;
  color: white;
}

.media-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px 20px;
  text-align: center;
}

.alert {
  width: 50%;
  margin: 20px auto;
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  padding: 15px;
  padding-right: 15px;
  border: 1px solid transparent;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
  border-radius: 4px;
  text-align: left;
}

i.close {
  text-decoration: none !important;
  font-size: 18px !important;
  line-height: 1.2;
  text-shadow: 0 1px 0 #fff;
  font-weight: 700;
  opacity: 0.2;
  color: #c4302b;
  cursor: pointer;
}

i.close:hover {
  opacity: 1;
  transition: 0.5s;
}

.alert-danger {
  color: #a94442;
  background-color: #f2dede;
  border-color: #ebccd1;
}
